import { Component, Host, h, Prop, State, JSX } from '@stencil/core';
import { RoutingKeys } from '../../entities/routing-key';
import {
  ClickElementTypes,
  EventActions,
  NavigationMode,
  NavigationModeType,
  NavigationTheme,
  NavigationThemeType
} from '../../utility/constants';
import { getRightTheme, IsLoggedIn } from '../../utility/helper';
import { navStateStore } from '../../state/nav-state-store';
import globalDataLayerService from '../../services/analytics/global-data-layer-service';

@Component({
  tag: 'phn-my-porsche-icon-button',
  styleUrl: 'my-porsche-icon-button.scss',
  scoped: true
})
export class MyPorscheIconButton {
  @Prop() mode: NavigationModeType = NavigationMode.navbar;
  @Prop() theme: NavigationThemeType = NavigationTheme.light;

  @State() isMyPorscheHoveredOn = false;

  private sendDataLayerEvent() {
    globalDataLayerService.pushDataLayerEvent(EventActions.CONTEXTUAL_BUTTON_CLICK, {
      componentClick: {
        clickElementId: RoutingKeys.MY_PORSCHE,
        clickElementName: 'open the my porsche menu',
        clickElementType: ClickElementTypes.Button
      }
    });
  }

  render(): JSX.Element {
    const { unreadMessagesCount, loggedInState } = navStateStore.state;
    const isLoggedIn = IsLoggedIn(loggedInState);

    return (
      <Host>
        <phn-p-button-pure
          icon={`user${isLoggedIn ? '-filled' : ''}`}
          hide-label="true"
          stretch="true"
          data-id={`${RoutingKeys.MY_PORSCHE}-contextual-button`}
          aria="{ 'aria-haspopup': 'dialog', 'aria-label' : 'open the my porsche menu' }"
          data-test-id="myporsche-button"
          theme={getRightTheme(this.theme, this.mode)}
          onMouseEnter={() => {
            this.isMyPorscheHoveredOn = true;
          }}
          onMouseLeave={() => {
            this.isMyPorscheHoveredOn = false;
          }}
          onClick={() => {
            this.sendDataLayerEvent();
          }}
        />
        {unreadMessagesCount > 0 ? (
          <span class={`myporsche-unread-messages-indicator ${getRightTheme(this.theme, this.mode)}`} />
        ) : null}
      </Host>
    );
  }
}
